<template>
  <div>
    <el-container>
      <el-aside style="width: 200px;min-height: 100vh ;background-color: #001529">
        <div style="height: 60px;color: white;display:flex; align-items: center;justify-content: center">
          企业工作台
        </div>
        <el-menu :default-active=$route.path active-text-color="#fff" background-color="#001529" router
                 style="border: none;background-color: red" text-color="rgba(255,255,255,0.65)">
          <el-menu-item index="/componentHomeView">
            <template slot="title">
              <i class="el-icon-house"></i>
              <span>系统首页</span>
            </template>
          </el-menu-item>
          <el-menu-item index="/componentInterview">面试通知</el-menu-item>

          <el-submenu>
            <template slot="title">
              <span>信息管理</span>
            </template>
            <el-menu-item index="companyInformation">查看/修改企业信息</el-menu-item>
            <el-menu-item index="componentPosts">查看所有岗位</el-menu-item>
            <el-menu-item index="companyPost">发布岗位</el-menu-item>
          </el-submenu>
          <el-menu-item index="/">用户退出</el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header></el-header>
        <el-main>
          <el-form  ref="form" :model="companyInformation" label-width="80px" style="width: 500px">

            <el-form-item label="企业账号">
              <el-input v-model="companyInformation.username"></el-input>
            </el-form-item>
            <el-form-item label="公司名称">
              <el-input v-model="companyInformation.name"></el-input>
            </el-form-item>

            <el-form-item label="公司地址">
              <el-input v-model="companyInformation.city"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="save">保存</el-button>
              <el-button type="danger">取消保存</el-button>
            </el-form-item>
          </el-form>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<style>
.el-menu--inline .el-menu-item {
  background-color: #000c17 !important;
}

.el-menu-item:hover, .el-submenu__title:hover {
  color: #fff !important;
}

.el-submenu__title:hover .el-icon-menu {
  color: #fff !important;
}

.el-menu-item.is-active {
  background-color: red !important;
}


</style>


<script>
import request from "@/utils/request";

export default {
  name: 'companyInformation',
  data() {
    return {
      companyInformation: {},
    }
  },
  methods: {
    save() {
      request.post('/component/update', this.companyInformation).then(res => {
            if (res.data.code === 1) {
              this.$message.success("保存成功")
              location.reload()
            } else {
              this.$message.error(res.data.message)
            }
          }
      )

    }
  },
  mounted() {
    request.get('/component/information').then(res => {
      this.companyInformation = res.data.data
    })
  }
}
</script>
